
*{
  font-family: 'Verdana', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.title-contr {
  position: fixed;
  text-align: center;
  background-color: white;
  z-index: 7;
}
.container{
  position: fixed;
  top: -10px;
  left: 0px;
  min-height: 80vh;
  width: 100%;
  text-align: center;
  margin: 10px auto;
  border: 3px solid #c3c3c3;
  background: conic-gradient(from 90deg at 2px 2px,#f5f4f4 90deg,#e0e0e0 0) -2px -2px/30px 30px;
}
.moving-hint-h2{
  margin-top: 40px!important;
  -webkit-animation: smooth 15s ease-out;
          animation: smooth 15s ease-out;
  color: #707070;
  opacity: 0.2;
}
@-webkit-keyframes smooth {
0% { opacity: 1;
}
100% { opacity: 0.2;
}
}
@keyframes smooth {
0% { opacity: 1;
}
100% { opacity: 0.2;
}
}
.square {
  height: 40px;
  width: 40px;
  background-color: #444444;
  display: inline-block;
  z-index: 5;
}
.triangle {
  width: 0;
  height: 0;
  border-left: 40px solid transparent;
  border-right: 40px solid transparent;
  border-bottom: 40px solid #444444;
  z-index: 5;
}
.circle {
  height: 40px;
  width: 40px;
  background-color: #444444;
  border-radius: 50%;
  display: inline-block;
  z-index: 5;
}
.dragging {
  position: absolute;
  pointer-events:none;
  background-color:#ff4f49;
  z-index: 10;
}
.dragging.triangle {
  border-bottom-color:#ff4f49;
  background-color: transparent;
}
.edit-panel {
  position: relative;
  top: -160px;
  left: -55px;
  width: 160px;
  height: 150px;
  padding:3px;
  background-color: white;
  z-index: 6;
  display: none;
  border: 1px solid #c0c0c0;
}
.triangle .edit-panel {
  left: -80px;
}
.edit-panel * {
  margin:5px 4px;
}
.edit-panel #sizeEditor {
  padding: 2px 2px 2px 15px;
  margin: 2px 0px 2px 11px;
}
.edit-panel button {
  margin-left: 6px;
  padding: 2px 10.5px;
}
.edit-panel .delete-button {
  color:#c42626;
}
.edit-panel .close-edit-button {
  padding: 2px 50px;
}
i.up,
i.left,
i.right,
i.down {
  position: relative;
  font-size: 2em;
  display: none;
  color: #747474;
}
i.up {
  top: -51px;
  left: 5px;
}
i.left{
  top: -19px;
  left: -57px;
}
i.right{
  top: -47px;
  left: 65px;
}
i.down{
  top: -8px;
  left: 5px;
}
.triangle i.up,
.triangle i.down{
  left: -17px!important;
}
.finish-moving-btn {
  position: relative;
  top: -107px;
  left: 5.2px;
  padding: 1px;
  width: 30px;
  height: 30px;
  font-size: 11pt;
  font-weight: bold;
  display: none;
  color: #383838;
}
.triangle .finish-moving-btn {
  left: -15.5px;
}
.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  padding: 20px;
  width: 100%;
  height: 20vh;
  border: 3px solid #c3c3c3;
  color: #707070;
  background-color: white;
  text-align: center;
  z-index: 9;
}
.footer div {
  display: inline-block;
  margin: auto 10px;
}
